export default {
  data() {
    return {
      // 昵称输入的内容
      nicknameVal: "",
      // 控制按钮的颜色
      isActive: false,
      // 控制清楚小图标的显示与隐藏
      isIcon: false
    };
  },
  methods: {
    // 当昵称框有输入内容的时候
    inputVal() {
      this.isActive = true;
      this.isIcon = true;
    },
    // 当密码框失去焦点的时候
    inputBlur() {
      if (this.nicknameVal !== "") {
        return (this.isActive = true);
      }
      return (this.isActive = false), (this.isIcon = false);
    },
    // 点击小图标清楚 input 里面的内容
    clearVal() {
      this.nicknameVal = "";
      this.isIcon = false;
    },
    submit() {
      this.$http
        .get("/user/updatenickName", { userId: 1, nickName: this.nicknameVal })
        .then(res => {
          console.log(res);
        });
      // uni.request({
      //   url: "http://192.168.1.143:8086/WNC/user/updatenickName",
      //   data: {
      //     userId: 1,
      //     nickName: this.nicknameVal
      //   },
      //   header: {
      //     "Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
      //   },
      //   success: res => {
      //     console.log(res);
      //   }
      // });
    }
  }
};
